import React, { Component } from 'react';
import { SegmentedControl, WingBlank, WhiteSpace, Grid } from 'antd-mobile';

// react component state update
import addons from 'react-addons-update';
import update from 'react-update';

import { MathPx } from '../utils/global';
import Page from '../components/page';
import LinkMy from '../components/linkMy';

import help from '../images/help.jpg';
import '../css/join.css';

class Infomation extends Component {
    constructor(props){
        super(props)
        this.update = update.bind(this);
        this.state={
            selectedSegmentIndex:0
        }
    }
    // 
    componentDidMount() {

    }
    render() {
        const _this = this;
        const state = _this.state;
        const update = _this.update;
        return (
            <Page>
                <div style={{height:MathPx(500)}} className="banner">
                    <img src={help} alt=""/>
                    <h3 style={{fontSize:MathPx(60),top:MathPx(150)}}></h3>
                    <p style={{fontSize:MathPx(24),top:MathPx(230)}}></p>
                </div>
                <div style={{paddingTop:MathPx(60)}} className="body">
                    <WingBlank>
                        <SegmentedControl selectedIndex={state.selectedSegmentIndex} onChange={(e)=>{
                            update('set',addons(state,{
                                selectedSegmentIndex:{
                                    $set:e.nativeEvent.selectedSegmentIndex
                                }
                            }))
                        }} tintColor={"#26b5ae"} values={['', '', '']} />
                        <WhiteSpace size="lg"/>
                        <WhiteSpace size="lg"/>
                        <WhiteSpace size="lg"/>
                        {
                            state.selectedSegmentIndex == 0 ?
                            <div className="about">
                                <div className="item-p">
                                    2016DDoS DDoS
2T
                                </div>
                                <div className="text-center">
                                    <img src="/static/img/Home/help/join-show.jpg" alt="" className="item-img"/>
                                </div>
                                <div className="item-p">
                                    DDoS7*24CCDDoS7*24CC
                                </div>
                            </div>:
                            state.selectedSegmentIndex == 1 ?
                            <div>
                                <ul className="join-attract">
                                    <li className="items">
                                        <div style={{borderBottomWidth:MathPx(10),height:MathPx(110),lineHeight:MathPx(110)+'px'}}>
                                            <i style={{fontSize:MathPx(17),marginRight:MathPx(10)}} className="icon iconfont icon-yuanyuansu"></i>
                                        </div>
                                        <p style={{fontSize:MathPx(24)}}>TIT</p>
                                        <WhiteSpace/>
                                    </li>
                                    <li className="items">
                                        <div style={{borderBottomWidth:MathPx(10),height:MathPx(110),lineHeight:MathPx(110)+'px'}}>
                                            <i style={{fontSize:MathPx(17),marginRight:MathPx(10)}} className="icon iconfont icon-yuanyuansu"></i>
                                        </div>
                                        <p style={{fontSize:MathPx(24)}}></p>
                                        <WhiteSpace/>
                                    </li>
                                    <li className="items">
                                        <div style={{borderBottomWidth:MathPx(10),height:MathPx(110),lineHeight:MathPx(110)+'px'}}>
                                            <i style={{fontSize:MathPx(17),marginRight:MathPx(10)}} className="icon iconfont icon-yuanyuansu"></i>
                                        </div>
                                        <p style={{fontSize:MathPx(24)}}></p>
                                        <WhiteSpace/>
                                    </li>
                                </ul>
                                <WhiteSpace/>
                                <WhiteSpace/>
                                <WhiteSpace/>
                                {/*<h3 style={{fontSize:MathPx(30)}} className="join-title-h3"></h3>
                                    <WhiteSpace/>
                                    <div style={{height:MathPx(70),lineHeight:MathPx(70)+'px',fontSize:MathPx(30)}} className="tab-title">
                                        <span></span>
                                        <span style={{marginLeft:MathPx(220)}}></span>
                                        <span style={{float:"right"}}>2018-05-09</span>
                                    </div>
                                    <WhiteSpace/>
                                    <WhiteSpace/>
                                    <h3 style={{fontSize:MathPx(24)}} className="join-title-h4"></h3>
                                    <p style={{fontSize:MathPx(24)}} className="join-text">A</p>
                                    <WhiteSpace/>
                                    <WhiteSpace/>
                                    <h3 style={{fontSize:MathPx(24)}} className="join-title-h4"></h3>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">1/
    </p>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">2
    </p>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">3
    PR</p>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">4
    </p>
                                    <WhiteSpace/>
                                    <WhiteSpace/>
                                    <h3 style={{fontSize:MathPx(24)}} className="join-title-h4"></h3>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">1.CET-427-40</p>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">2.5
    B2C</p>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">3.
    </p>
                                    <p style={{fontSize:MathPx(24),margin:MathPx(0),lineHeight:MathPx(44)+'px'}} className="join-text">4.</p>*/}
                            </div>:
                            state.selectedSegmentIndex == 2 ?
                            <div>
                                <Grid data={[{
                                    img:'/static/img/Home/bys.png',
                                    to:'https://www.baishancloud.com/zh/'
                                },{
                                    img:'/static/img/Home/kzw.png',
                                    to:'http://www.kongzhong.com'
                                },{
                                    img:'/static/img/Home/zxwa.png',
                                    to:'http://www.cnzxsoft.com/index.html'
                                },{
                                    img:'/static/img/Home/shwyw.png',
                                    to:'http://www.7x24.cn/sales/'
                                },{
                                    img:'/static/img/Home/ydzs.png',
                                    to:'https://www.isurecloud.net'
                                },{
                                    img:'/static/img/Home/fj6.png',
                                    to:'https://www.6.cn'
                                },{
                                    img:'/static/img/Home/qmhd.png',
                                    to:'http://www.quanmin-game.com'
                                },{
                                    img:'/static/img/Home/fhjr.png',
                                    to:'https://www.fengjr.com/cn/'
                                }]}
                                columnNum={3}
                                className="grid"
                                renderItem={dataItem => (
                                    <a target="_blank" style={{position:"absolute",width:"100%",height:"100%",left:0,top:0,alignItems:"center",display:"flex",justifyContent:"center"}} to={dataItem.to}>
                                        <img width="80%" src={dataItem.img} alt="" />
                                    </a>
                                )}/>
                                <WhiteSpace/>
                                <WhiteSpace/>
                            </div>
                            :""
                        }
                        
                    </WingBlank>
                </div>
                <LinkMy/>
            </Page>
        );
    }
}

export default Infomation;



