import React, { Component } from 'react';
import { SegmentedControl, WingBlank, WhiteSpace } from 'antd-mobile';
import { withRouter } from 'react-router';

// react component state update
// import addons from 'react-addons-update';
import update from 'react-update';

import { MathPx } from '../utils/global';
import '../css/help.css';

import Page from '../components/page';
import LinkMy from '../components/linkMy';

import help from '../images/help.jpg';

class Help extends Component {
    constructor(props){
        super(props)
        this.update = update.bind(this);
        this.state={
            showDetail:false,
            index:0,
            selectedSegmentIndex:0,
            list:[{
                title:"",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:1,
                body:"2016DDoSDDoS2T"
            },{
                title:"D",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:1,
                body:"DDNSDNS"
            },{
                title:"",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:1,
                body:""
            },{
                title:"Dipping",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:1,
                body:"ipip"
            },{
                title:"D",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:1,
                body:""
            },{
                title:"D",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:1,
                body:"5GDDoS90%5G5GDDoS"
            },{
                title:"",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:2,
                body:"zealsafe.com''<br/><br/><br/><br/><img src='/static/img/Home/help/help-acount-1.png' alt=''><br/><br/>''''<br/><br/><img src='/static/img/Home/help/help-acount-2.png' alt=''>"
            },{
                title:"",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:2,
                body:"''<br/><br/><img src='/static/img/Home/help/help-acount-3.png'><br/><br/>''''<br/><br/><img src='/static/img/Home/help/help-acount-4.png'><br/><br/><img src='/static/img/Home/help/help-acount-5.png'>"
            },{
                title:"D",
                years:"2018",
                month:"02-06",
                ctime:"2018 02-06",
                type:3,
                body:"''<br/><br/><img src='/static/img/Home/help/help-dns-1.png'><br/><br/>''<br/><br/><img src='/static/img/Home/help/help-dns-2.png'><br/><br/>''''CNAME<br/><br/><img src='/static/img/Home/help/help-dns-3.png'>CNAMEDNSDNSPODcname''<br/><br/><img src='/static/img/Home/help/help-dns-4.png'>DNSCNAMED<br/><br/><img src='/static/img/Home/help/help-dns-5.png'>"
            },{
                title:"",
                years:"2018",
                month:"02-06",
                type:4,
                body:"<p>5GD5G QQ4008989875</p><p><img src='/static/img/Home/help/help-moredef-1.jpg'></p>"
            }]
        }
    }
    //
    componentDidMount() {
       
    }
    render() {
        const _this = this;
        const state = _this.state;
        const update = _this.update;
        const { history } = _this.props;
        return (
            <Page>
                <div style={{height:MathPx(500)}} className="banner">
                    <img src={help} alt=""/>
                    <h3 style={{fontSize:MathPx(60),top:MathPx(150)}}></h3>
                    <p style={{fontSize:MathPx(24),top:MathPx(230)}}></p>
                    <p style={{fontSize:MathPx(24),top:MathPx(280)}}></p>
                </div>
                <div style={{paddingTop:MathPx(60),paddingBottom:MathPx(60)}} className="body">
                    <WingBlank>
                        <SegmentedControl selectedIndex={state.selectedSegmentIndex} onChange={(e)=>{
                            update('set','selectedSegmentIndex',e.nativeEvent.selectedSegmentIndex);
                        }} tintColor={"#26b5ae"} values={['', '', '', 'DNS', '']} />
                        <WhiteSpace size="lg"/>
                        <WhiteSpace size="lg"/>
                        {
                            state.list.map((el,index)=>{
                                let isDisplay = state.selectedSegmentIndex == 0 || state.selectedSegmentIndex == el.type;
                                return (
                                    <dl onClick={()=>{
                                        history.push({
                                            pathname: 'detail.html',
                                            desc:el
                                        })
                                    }} key={index} style={{borderBottom:"1px solid #ddd",display:isDisplay ? 'block':'none'}} className="clearfix">
                                        <dt style={{fontSize:MathPx(36),width:MathPx(500),float:"left"}}>
                                            <h4 style={{color: '#333',fontSize: MathPx(36)}}>{el.title}</h4>
                                        </dt>
                                        <dd style={{float:"right",width:MathPx(130),marginLeft:0,textAlign:"right"}}>
                                            <h4 style={{color:"#999",fontSize:MathPx(36)}}>{el.years}</h4>
                                            <p style={{color:"#999",fontSize:MathPx(24)}}>{el.month}</p>
                                        </dd>
                                    </dl>
                                )
                            })
                        }
                    </WingBlank>
                </div>
                <LinkMy/>
            </Page>
        );
    }
}

export default withRouter(Help);



