import React, { Component } from 'react';
import { WhiteSpace, WingBlank, Badge, Modal } from 'antd-mobile';

// react component state update
// import addons from 'react-addons-update';
import update from 'react-update';

import { MathPx, Ajax } from '../utils/global';
import { config } from '../utils/config';

import Page from '../components/page';
import LinkMy from '../components/linkMy';

import help from '../images/help.jpg';
import '../css/product.css';

class Product extends Component {
    constructor(props){
        super(props)
        this.update = update.bind(this);
        this.state={
            productListData:[],
            modal:false,
            title:'',
            index:0,
            text:''
        }
    }
    // 
    componentDidMount() {
        const _this = this;
        Ajax.get(config.Product.urls.productList,(response)=>{
            if(response.code == 1){
                _this.update('set',{
                    productListData:response.data.base
                })
            }
        },true)
    }
    render() {
        const _this = this;
        const state = _this.state;
        const update = _this.update;
        return (
            <Page>
                <div style={{backgroundColor:"#fff"}}>
                    <div style={{height:MathPx(500)}} className="banner">
                        <img src={help}/>
                        <h3 style={{fontSize:MathPx(60),top:MathPx(150)}}></h3>
                        <p style={{fontSize:MathPx(24),top:MathPx(230)}}></p>
                        <p style={{fontSize:MathPx(24),top:MathPx(280)}}></p>
                    </div>
                    <WingBlank>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <h3 style={{fontSize:MathPx(36)}} className="product-title">
                            {/*<a style={{fontSize:MathPx(24)}} href="#" target="_blank"></a>*/}
                        </h3>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <ul style={{paddingTop:MathPx(30),paddingBottom:MathPx(30),paddingLeft:MathPx(20),paddingRight:MathPx(20),fontSize:MathPx(24)}} className="product-list-title clearfix">
                            <li></li>
                            <li>/</li>
                            <li onClick={()=>{
                                update('set',{
                                    modal:true,
                                    text:'DDoSbps',
                                    title:''
                                })
                            }}><Badge size="small" style={{backgroundColor:"#26b5ae",position:"absolute",top:MathPx(-40),right:MathPx(-30)}} text={'?'}></Badge></li>
                            <li onClick={()=>{
                                update('set',{
                                    modal:true,
                                    text:'CCQPS',
                                    title:'CC'
                                })
                            }}>CC<Badge size="small" style={{backgroundColor:"#26b5ae",position:"absolute",top:MathPx(-40),right:MathPx(-30)}} text={'?'}></Badge></li>
                            <li onClick={()=>{
                                update('set',{
                                    modal:true,
                                    text:'Byte',
                                    title:''
                                })
                            }}><Badge size="small" style={{backgroundColor:"#26b5ae",position:"absolute",top:MathPx(-40),right:MathPx(-30)}} text={'?'}></Badge></li>
                        </ul>
                        {
                            state.productListData.map((el,index)=>(
                                <ul key={index} style={{fontSize:MathPx(24),marginTop:MathPx(20)}}
                                onClick={()=>{
                                    update('set',{
                                        index
                                    })
                                }}
                                className={state.index == index ? "hover product-list-record clearfix" : "product-list-record clearfix"}>
                                    <li style={{paddingTop:MathPx(30),paddingBottom:MathPx(30),paddingLeft:MathPx(20)}} className="high">
                                        {el.name}
                                    </li>
                                    <li style={{paddingTop:MathPx(30),paddingBottom:MathPx(30)}} className="high">
                                        {el.price == 0 ? '' : el.price}
                                    </li>
                                    <li style={{paddingTop:MathPx(30),paddingBottom:MathPx(30)}}>
                                        {el.cap}G
                                    </li>
                                    <li style={{paddingTop:MathPx(30),paddingBottom:MathPx(30)}}>
                                        {el.cc}
                                    </li>
                                    <li style={{paddingTop:MathPx(30),paddingBottom:MathPx(30),paddingRight:MathPx(20)}}>
                                        {el.flow}G
                                    </li>
                                </ul>
                            ))
                        }
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <h3 style={{fontSize:MathPx(36)}} className="product-title">
                            D<a style={{fontSize:MathPx(24)}} href="#" target="_blank">3650/</a>
                        </h3>
                        <WhiteSpace/>
                        <WhiteSpace/>
                        <table style={{fontSize:MathPx(24)}} className="table">
                            <thead>
                                <tr style={{textIndent:MathPx(16)}} height={MathPx(80)}>
                                    <th className="high">
                                        <i style={{fontSize:MathPx(24),marginRight:MathPx(10)}} 
                                            className="icon iconfont icon-yuanyuansu"></i>
                                        
                                    </th>
                                    <th>
                                        <i style={{fontSize:MathPx(24),marginRight:MathPx(10)}} 
                                            className="icon iconfont icon-yuanyuansu"></i>
                                        
                                    </th>
                                    <th>
                                        <i style={{fontSize:MathPx(24),marginRight:MathPx(10)}} 
                                            className="icon iconfont icon-yuanyuansu"></i>
                                        
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td></td>
                                    <td className="high">20Gbps</td>
                                    <td className="high" onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:'20Gbps'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td className="high">CC</td>
                                    <td>60,000QPS</td>
                                    <td onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'CC',
                                            text:'60,000QPS'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td></td>
                                    <td className="high">3</td>
                                    <td className="high" onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:''
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td className="high"></td>
                                    <td>20Mbps</td>
                                    <td onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:'20M'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td>QPS</td>
                                    <td className="high">3,000QPS</td>
                                    <td className="high" onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'QPS',
                                            text:'QPS'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td className="high"></td>
                                    <td>1</td>
                                    <td onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:''
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td></td>
                                    <td className="high">10</td>
                                    <td className="high" onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:'www,@,*3'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td className="high"></td>
                                    <td>10</td>
                                    <td onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:'IPIP5IPIP'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td></td>
                                    <td className="high">
                                        <p>/:80/http</p>
                                        <p>/:443/https</p>
                                    </td>
                                    <td className="high" onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:'8044380http443httpshttps'
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                                <tr style={{textIndent:MathPx(50)}} height={MathPx(80)}>
                                    <td className="high"></td>
                                    <td>7x24</td>
                                    <td onClick={()=>{
                                        update('set',{
                                            modal:true,
                                            title:'',
                                            text:''
                                        })
                                    }}><a href="javascript:;"></a></td>
                                </tr>
                            </tbody>
                        </table>
                        <WhiteSpace/>
                    </WingBlank>
                </div>
                <LinkMy/>
                <Modal
                    visible={state.modal}
                    transparent
                    maskClosable={false}
                    title={state.title}
                    footer={[{ text: '', onPress: () => { 
                        update('set',{
                            modal:false
                        })
                    }}]}>
                    {state.text}
                </Modal>
            </Page>
        );
    }
}

export default Product;



