/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[Number]}   px []
 * @return {[Number]}      []
 */
const initWidth = 750;
const currentWidth = window.innerWidth;
export const MathPx = (px)=>{
	return px/(initWidth/currentWidth)
}

/*
 * application/x-www-form-urlencoded 
 * @param key {String}: key
 * @param arr {Array}: 
 * @return {String}
 */
const formatRequestArray = (key, arr) => {
	let con = '';
	arr.forEach((el) => {
		con += key+'[]='+el+'&';
	});
	return con;
}

/*
 *  application/x-www-form-urlencoded 
 * @param json {Object}:  json
 * @return {String}
 */
const formatRequestParam = (json)=>{
	let con = '';
	for( let key in json ) {
		const val = json[key];
		const type  = typeof val;
		
		if( /string|number/.test(type) ) {
			con += encodeURIComponent(key)+'='+encodeURIComponent(val)+'&';
		} else if( val instanceof Array ) {
			con += formatRequestArray(key, val);
		}
	}
	return con.substring(0, con.length - 1);
}
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     []
 * @param  {[Object]}   params  []
 * @param  {[Boolean]}  completeData  []
 * @param  {[Function]} resolve []
 */
const post = (url,params,resolve,completeData)=>{
	let data = Object.keys(params).length ? params : {};
	fetch(url,{
	    type:'post',
	    method:'post',
	    credentials: "include",
	    body:formatRequestParam(data),
	    headers:{
	        'Accept': 'application/x-www-form-urlencoded',
	        'Content-Type': 'application/x-www-form-urlencoded',
	    }
	}).then((response)=>{
	    if( !response.ok ) console.log('');
	    return response.json();
	}).then((json)=>{
		if(json.code==200 && !completeData){
	    	resolve(json.data)
		}else{
			resolve(json)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     []
 * @param  {[Boolean]}  completeData  []
 * @param  {[Function]} resolve []
 */
const get = (url,resolve,completeData)=>{
	fetch(url,{
		type:'get',
	    credentials: "include",
		method:'get'
	}).then((response)=>{
		if( !response.ok ) console.log('');
		return response.json();
	}).then((json)=>{
		if(json.code==200 && !completeData){
			resolve(json.data)
		}else{
			resolve(json)
		}
	}).catch((ex)=>{
		console.trace(ex);
	})
}
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     []
 * @param  {[Object]}   params  []
 * @param  {[Boolean]}  completeData  []
 * @param  {[Function]} resolve []
 */
const file = (url,params,resolve,completeData)=>{
	let body = Object.keys(params).length ? params : {};
	fetch(url,{
	    type:'file',
	    credentials: "include",
	    method:'post',
	    body
	}).then((response)=>{
	    if( !response.ok ) console.log('');
	    return response.json();
	}).then((json)=>{
	    if(json.code==200 && !completeData){
	    	resolve(json.data)
		}else{
			resolve(json)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}

export const Ajax = {
	post,
	get,
	file
}


// 
export let GLOBAL={
	menu:[]
}
export const setMenu = data =>{
	GLOBAL.menu = data;
}




